<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
                   color="rgba(var(--vs-primary), 1)"
                   :title="null" :subtitle="null"
                   :nextButtonText="$t('NEXT')"
                   :backButtonText="$t('PREVIOUS')">
        <template slot="custom-buttons-left" slot-scope="props">
          <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
        </template>
        <template slot="finish">
          <div class="dropdown-button-container">
            <vs-button class="btnx" type="filled" @click="createTeller('browse')">{{ $t('SUBMIT') }}</vs-button>
            <vs-dropdown vs-trigger-click>
              <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="createTeller('stay')">
                  <span>{{ $t('SUBMIT_STAY') }}</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="createTeller('view')">
                  <span>{{ $t('SUBMIT_VIEW') }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </template>

        <tab-content :title="$t('GENERAL')" icon="far fa-file-alt">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w w-full required">
              <label class="vs-input--label">{{ $t('TELLER_NAME') }}</label>
              <vs-input class="w-full" v-model="tellerName"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('TELLER_TYPE') }}</label>
              <teller-type-lookup :title="$t('TELLER_TYPE')" valueField="typeId" displayField="typeName" @select-record="selectTellerTypeRecord"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('TELLER_STATUS') }}</label>
              <teller-status-lookup :title="$t('TELLER_STATUS')" valueField="statusId" displayField="statusName" @select-record="selectTellerStatusRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('PSEUDO_ID') }}</label>
              <vs-input class="w-full" v-model="pseudoId"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
              <vs-input class="w-full" v-model="externalId"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w w-full">
              <vs-checkbox v-model="disabled">{{ $t('DISABLED') }}</vs-checkbox>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('ADDRESS')" icon="far fa-map-marked">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('LATITUDE') }}</label>
              <vs-input class="w-full" v-model="latitude"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('LONGITUDE') }}</label>
              <vs-input class="w-full" v-model="longitude"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('ELEVATION') }}</label>
              <div class="flex">
                <div class="flex mr-4">
                  <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="elevationUomId">
                    <vs-select-item v-for="(uom, index) in lengthUoms" :key="index" :value="uom.uomId" :text="uom.description"/>
                  </vs-select>
                </div>
                <div class="flex">
                  <vs-input-number v-model="elevation"/>
                </div>
              </div>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('FUNCTIONS')" icon="far fa-check">
          <ul class="flex justify-start">
            <li class="mr-5" v-for="(bankFunction, index) in bankFunctionList" :key="index">
              <vs-checkbox v-model="functions" :vs-value="bankFunction.functionId" >
                <i class="text-primary mr-1" :class="bankFunction.iconClass"></i> <span :class="disabledClass(bankFunction)">{{ bankFunction.functionName }}</span>
              </vs-checkbox>
            </li>
          </ul>
        </tab-content>
      </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import TellerService from '@/services/bank/TellerService'
import TellerTypeLookup from '@/views/bank/teller-type/TellerTypeLookup'
import TellerStatusLookup from '@/views/bank/teller-status/TellerStatusLookup'
import UomService from '@/services/commons/UomService'
import BankFunctionService from '@/services/bank/BankFunctionService'
import { FormWizard, TabContent } from 'vue-form-wizard'

export default {
  name: 'teller-create',
  mixins: [AlertMixin, DisabledClassMixin, LoadingMixin],
  components: {
    FormWizard,
    TabContent,
    TellerTypeLookup,
    TellerStatusLookup
  },
  data: function () {
    return {
      tellerName: '',
      pseudoId: '',
      externalId: '',
      disabled: false,
      latitude: '',
      longitude: '',
      elevation: 0,
      elevationUomId: 'LEN_m',
      lengthUoms: [],
      functions: [],
      bankFunctionList: []
    }
  },
  methods: {
    async load () {
      let self = this
      UomService.uoms({
        uomTypeEnumId: 'UT_LENGTH_MEASURE',
        orderByField: 'description',
        pageNoLimit: true
      }).then(function (response) {
        self.lengthUoms = response.data.uomList || []
      })

      BankFunctionService.bankFunctions({
      }).then(function (response) {
        self.bankFunctionList = response.data.functionList || []
      })
    },
    selectTellerTypeRecord (record) {
      this.tellerTypeId = record.typeId || ''
    },
    selectTellerStatusRecord (record) {
      this.tellerStatusId = record.statusId || ''
    },
    createTeller (nextAction) {
      let self = this
      this.startLoading('loading-container')
      TellerService.createTeller({
        tellerName: this.tellerName,
        tellerTypeId: this.tellerTypeId,
        tellerStatusId: this.tellerStatusId,
        pseudoId: this.pseudoId,
        externalId: this.externalId,
        disabled: this.disabled ? 'Y' : 'N',
        latitude: this.latitude,
        longitude: this.longitude,
        elevation: this.elevation,
        elevationUomId: this.elevationUomId,
        functions: this.functions.join(',')
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
            self.$refs.wizard.reset()
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewTeller', params: { id: response.data.tellerId } })
          } else {
            self.$router.push({ name: 'BrowseTellers' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  }
}
</script>
